.sssd {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: linear-gradient(
    rgba(5, 5, 5) 0%,
    rgb(22, 22, 22) 69%,
    rgb(24, 24, 24) 89%)

  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1, h2 {
  color: white;
  font-family: 'Lato', sans-serif;
  font-weight: 400;
}


.ui.top.fixed.menu {
  opacity: 0.8;
  background-color: white;
}



.ui.header.home-header {
  padding: 10px;
  font-size: 2.5em !important;
  letter-spacing: 2px;
  color: #6F4839;
  text-transform: uppercase;
}

.ui.button.ss {
  letter-spacing: 2px;
  border-radius: 0%;
  text-transform: uppercase;
  background-color: #6F4839;
}

.ui.basic.button.ttt {
  color: #6F4839 !important;
  box-shadow: 0 0 0 1px #6F4839 !important;
  letter-spacing: 2px;
  border-radius: 0%;
  text-transform: uppercase;
}

.ui.button.fb {
  letter-spacing: 2px;
  border-radius: 0%;
  text-transform: uppercase;
}

@media only screen and (max-width: 800px) {
  .gsd {
    width: 90% !important;
    height: 45% !important;
  }

  .ui.text.left.aligned.container {
    padding: 0 !important;
  }
}